.gallery {
  padding-top: 230px;

    //   transition: justify-content 0.3s ease;
  @include tablet {
    padding-top: 213px;
  }
  &__list {
    // margin: 0;
    padding: 0;
    // max-width: 280px;


    width: 100%;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
   
    background-image: repeating-linear-gradient(
      to right,
      getColor('footer-bg'),
      getColor('footer-bg') 130px,
      getColor('text-1') 130px,
      getColor('text-1') 150px
    );

    @include tablet {
      gap: 32px;
      // max-width: 336px;
    }
    @include desktop {
      gap: 16px;
      // max-width: 395px;
    }

    &-item {
      background-color:getColor('footer-bg');
      border-radius: 5px;
      height: 443px;
      width: 280px;
      transition: all 0.3s ease;
      transform: perspective(1000px) scale(1) translateZ(0);
      //transform: perspective(1000px) scale(1) translateZ(0);
      position: relative;
      &:hover {
        transform: perspective(1000px) scale(1.01) translateZ(20px);
        box-shadow: 0 0 30px rgba(195, 132, 22, 0.691); 
        position: relative;   
        z-index: 1;
      }

      @include tablet {
        height: 494px;
        width: 336px;
      }

      @include desktop {
        height: 634px;
        width: 394px;
      }

      > img {
        display: block;
        border-radius: 10px;
        width: 100%;
        cursor: pointer;
        height: 402px;
        max-width: 280px;
        object-fit: cover;

        @include tablet {
          max-width: 336px;
          height: 455px;
        }

        @include desktop {
          max-width: 395px;
          height: 574px;
        }
      }
      > h3 {
        font-size: 12px;
        line-height: 16px;
        padding-top: 10px;
        padding-left: 5px;
        position: relative;
      }
      > p {
        color: getColor('button-activebg');
        font-size: 12px;
        line-height: 16px;
    
        padding-left: 5px;
      }
      > div {
       // display: none;
        width: 26px;
        color: getColor('text-1');
        background-color: getColor('active');
        padding: 5px;
        border-radius: 5px;
        position: absolute;
        bottom: 10px;
  right: 10px;
  @include tablet {
 
  }

  @include desktop {
    bottom: 15px;
    right: 15px;
  }

      }
    }
  }
}
